import React from 'react';
import { Container } from '@mui/material';
import { makeStyles } from '@mui/styles';
import officeImage from '../../assets/images/AboutUs/office.png';
import finamLogo from '../../assets/images/AboutUs/finam.png';
import mtbankLogo from '../../assets/images/AboutUs/mtbank.png';
import softClubLogo from '../../assets/images/AboutUs/softclub.png';
import bntechLogo from '../../assets/images/AboutUs/bntech.png';

const useStyles = makeStyles(() => ({
    container: {
        marginTop: '80px',
        marginBottom: '80px',
        maxWidth: '1050px !important',
    },
    title: {
        fontSize: '72px',
        fontWeight: '700',
        marginBottom: '35px',
        fontFamily: 'Oswald',
        '@media (max-width: 960px)': {
            fontSize: '42px',
            lineHeight: '48px',
        },
    },
    introSection: {
        display: 'grid',
        gridTemplateColumns: '1fr 1fr',
        gap: '40px',
        marginBottom: '80px',
        '@media (max-width: 960px)': {
            gridTemplateColumns: '1fr',
        },
    },
    introText: {
        '& p': {
            fontSize: '18px',
            lineHeight: '1.5',
            marginBottom: '20px',
        },
    },
    introBlock: {
        position: 'relative',
        minWidth: '480px',
        maxWidth: '100%',
        width: 'auto',
        height: '200px',
        background: 'radial-gradient(66.71% 66.71% at 50% 50%, #EDF4FF 0%, #FFFFFF 100%)',
        boxShadow: '4px 5px 6px rgba(0, 0, 0, 0.05)',
        borderRadius: '25px',
        padding: '32px',
        marginBottom: '40px',
        overflow: 'hidden',
        '& p': {
            fontSize: '18px',
            lineHeight: '1.5',
            margin: 0,
            color: '#000',
            '&:first-of-type': {
                marginBottom: '20px',
            }
        },
        '& strong': {
            fontWeight: 'bold'
        }
    },
    introImage: {
        display: 'flex',
        alignItems: 'flex-end',
        height: '100%',
        '& img': {
            width: '100%',
            borderRadius: '25px',
            display: 'block',
            marginLeft: 'auto',
            marginBottom: '40px'
        },
    },
    partnersSection: {
        marginBottom: '80px',
        background: '#F7FAFF',
        padding: '40px',
        borderRadius: '0',
        marginLeft: 'calc(-50vw + 50%)',
        marginRight: 'calc(-50vw + 50%)',
        width: '100vw',
        '& > h2': {
            maxWidth: '1050px',
            margin: '0 auto 40px',
        },
        '& > div': {
            maxWidth: '1050px',
            margin: '0 auto',
        }
    },
    sectionTitle: {
        fontSize: '42px',
        fontWeight: '700',
        marginBottom: '40px',
        fontFamily: 'Oswald',
        color: '#0D2C4A',
        textAlign: 'center',
    },
    partnerCard: {
        display: 'flex',
        alignItems: 'center',
        gap: '20px',
        marginBottom: '30px',
    },
    partnerLogo: {
        width: '120px',
        flexShrink: 0,
    },
    partnerDescription: {
        fontSize: '16px',
        lineHeight: '1.5',
        color: '#4A4A4A',
    },
    offerSection: {
        marginBottom: '80px',
        '& > h2': {
            color: '#000000',
            textAlign: 'left',
            maxWidth: '1050px',
            margin: '0 auto 40px',
        }
    },
    offerItem: {
        marginBottom: '30px',
        margin: '0 auto',
        position: 'relative',
        minWidth: '480px',
        maxWidth: '100%',
        width: 'auto',
        background: 'radial-gradient(66.71% 66.71% at 50% 50%, #EDF4FF 0%, #FFFFFF 100%)',
        boxShadow: '4px 5px 6px rgba(0, 0, 0, 0.05)',
        borderRadius: '25px',
        padding: '32px',
        overflow: 'hidden',
        '& p': {
            fontSize: '28px',
            lineHeight: '1.5',
            color: '#000000',
            fontFamily: 'Oswald',
            margin: 0,
            '& strong': {
                fontWeight: '700',
            }
        },
    },
    techExpertise: {
        textAlign: 'center',
        marginBottom: '80px',
        background: '#F7FAFF',
        padding: '40px',
        borderRadius: '0',
        marginLeft: 'calc(-50vw + 50%)',
        marginRight: 'calc(-50vw + 50%)',
        width: '100vw',
        '& > h2': {
            maxWidth: '1050px',
            margin: '0 auto 40px',
        },
        '& > p': {
            fontSize: '18px',
            lineHeight: '1.5',
            maxWidth: '800px',
            margin: '0 auto 40px',
            '& strong': {
                fontWeight: 'bold'
            }
        },
        '& > div': {
            maxWidth: '1050px',
            margin: '0 auto',
        }
    },
    advantages: {
        background: 'radial-gradient(66.71% 66.71% at 50% 50%, #EDF4FF 0%, #FFFFFF 100%)',
        boxShadow: '4px 5px 6px rgba(0, 0, 0, 0.05)',
        padding: '32px',
        borderRadius: '25px',
        '& > h2': {
            fontFamily: 'Open Sans',
            fontWeight: 800,
            fontSize: '31px',
            lineHeight: '34px',
            letterSpacing: '0.2px',
            marginBottom: '40px',
            color: '#000000',
            textAlign: 'left',
        },
        '& ul': {
            listStyle: 'none',
            padding: 0,
            '& li': {
                fontFamily: 'Oswald',
                fontWeight: 300,
                fontSize: '18px',
                lineHeight: '34px',
                letterSpacing: '0.2px',
                marginBottom: '15px',
                paddingLeft: '20px',
                position: 'relative',
                '&:before': {
                    content: '""',
                    position: 'absolute',
                    left: 0,
                    color: '#000000',
                },
            },
        },
    },
}));

export const AboutUs: React.FC = () => {
    const classes = useStyles();

    return (
        <Container className={classes.container}>
            <div className={classes.introSection}>
                <div className={classes.introText}>
                    <h1 className={classes.title}> ?</h1>
                    <div className={classes.introBlock}>
                        <p><strong>      ,   2024           .</strong></p>
                        <p>             .</p>
                    </div>
                </div>
                <div className={classes.introImage}>
                    <img src={officeImage} alt="Office" />
                </div>
            </div>

            <div className={classes.partnersSection}>
                <h2 className={classes.sectionTitle}>  </h2>
                <div className={classes.partnerCard}>
                    <img src={finamLogo} alt="Finam" className={classes.partnerLogo} />
                    <p className={classes.partnerDescription}>
                             30-  ,   500 000 .   1994          .
                    </p>
                </div>
                <div className={classes.partnerCard}>
                    <img src={mtbankLogo} alt="MTBank" className={classes.partnerLogo} />
                    <p className={classes.partnerDescription}>
                               ,      1994 .   -10        .
                    </p>
                </div>
            </div>

            <div className={classes.offerSection}>
                <h2 className={classes.sectionTitle}>  </h2>
                <div className={classes.offerItem}>
                    <p><strong> </strong> -        ,      </p>
                </div>
                <div className={classes.offerItem}>
                    <p><strong> </strong> -     ,   </p>
                </div>
                <div className={classes.offerItem}>
                    <p><strong> </strong> -          </p>
                </div>
            </div>

            <div className={classes.techExpertise}>
                <h2 className={classes.sectionTitle}> </h2>
                <p><strong>       -     :</strong></p>
                <div className={classes.partnerCard}>
                    <img src={softClubLogo} alt="SoftClub" className={classes.partnerLogo} />
                    <p className={classes.partnerDescription}>
                           30-           .
                    </p>
                </div>
                <div className={classes.partnerCard}>
                    <img src={bntechLogo} alt="BNTech" className={classes.partnerLogo} />
                    <p className={classes.partnerDescription}>
                          ,      - .
                    </p>
                </div>
            </div>

            <div className={classes.advantages}>
                <h2 className={classes.sectionTitle}> </h2>
                <ul>
                    <li>,     </li>
                    <li>     </li>
                    <li>    blockchain </li>
                    <li>       </li>
                    <li>      </li>
                </ul>
            </div>
        </Container>
    );
}; 